import React, { Component } from 'react';
import styled from 'styled-components';
import { BrowserRouter as Router, Route, Switch, withRouter } from 'react-router-dom';
import Info from './info';
import store from '../store';
import Detail from './contentFolder/detail';
import Edu from './contentFolder/edu';
import Work from './contentFolder/work';
import Lang from './contentFolder/lang';
import Skill from './contentFolder/skill';
import Home from './contentFolder/home';
import Unauth from './contentFolder/unauthorized'

const CurPage = styled.div`
	color: #ccc;
	top: ${ window.innerWidth>960 ? 130 : 100 }px;
	position: absolute;
	background: #333;
	width: ${ window.innerWidth>960 ? 960 : window.innerWidth }px;
	height: ${ window.innerWidth>960? 490 : window.innerHeight-130 }px;
	padding-top: 30px;
	overflow: auto;
`
const MainSteam = styled.div`
	font-size: 24px;
	margin: 0px 0px 15px 40px;
	margin-left: ${window.innerWidth<=320? '20px' : '40px'} !important;
	width: ${ window.innerWidth>960 ? 960*0.6 : window.innerWidth *0.8 }px;
	border-bottom: solid 1px #ccc;
	padding-bottom: 5px;
`

const Title = styled.span`
	font-weight: bold;
	font-size: 24px;
`
const Output = props => {
	switch (props.toRender) {
		case 'detail':
			return (
				<Detail />
			)
			break;
		case 'edu':
				return (
					<Edu />
				)
				break;
		case 'work':
				return (
					<Work />
				)
				break;
		case 'lang':
				return (
					<Lang />
				)
				break;
		case 'skill':
				return (
					<Skill />
				)
				break;
		case 'home':
				return (
					<Home />
				)
				break;
		default:
			return (
				<Unauth />
			)
	}
}

class PageInfo extends Component {

	constructor(props){
		super(props);
		window.innerHeight>650 && window.innerWidth>960 ? window.addEventListener('wheel', this.scroll.bind(this)) : ( () => {
			window.addEventListener('touchstart', this.touchStart.bind(this))
			window.addEventListener('touchend', this.touchEnd.bind(this))
			}
		);
	}
	touchProcesser( type, location ){
		console.log('type: ', type);
		console.log('location: ', location);
		let onStartWidth = 0;
		if (type === 'down') {
			onStartWidth = location;

		} else {
			let index = onStartWidth > location ? (this.props.pageGo.index <5 ? this.props.pageGo.index +1 : this.props.pageGo.index ) : (this.props.pageGo.index >0 ? this.props.pageGo.index -1 : this.props.pageGo.index);
			let findInfo = Info.find( x => x.index === index );
			store.dispatch( {type:'route', data: findInfo.path, index: index} );
		}
	}

	touchStart(e) {
		let start = e.lientX;
		this.touchProcesser( 'down', start );
	}
	touchEnd(e) {
		let end = e.clientX;
		this.touchProcesser( 'up', end );
	}

	scroll(e) {
		let index = e.deltaY>0 ? (this.props.pageGo.index <5 ? this.props.pageGo.index +1 : this.props.pageGo.index ) : (this.props.pageGo.index >0 ? this.props.pageGo.index -1 : this.props.pageGo.index);
		let findInfo = Info.find( x => x.index === index );
	  store.dispatch( {type:'route', data: findInfo.path, index: index} );
	  this.props.history.push( '/portfolio/'+ findInfo.path);
	}
	hideMenu(){
		window.innerWidth>960 ? '' :  store.dispatch( {type:'menu', toShowBool: false, data: this.props.pageGo.path, index: this.props.pageGo.index } );
	}

	render() {
		this.props.match.path === '/' ? this.props.history.push( '/portfolio/') : console.log('No redirection needed!');
		this.props.pageGo ? store.dispatch( {type:'route', data: this.props.pageGo.path, index: this.props.pageGo.index} ) : console.log('props is undefined');
		return (
			<CurPage onClick={this.hideMenu.bind(this)}>
				<MainSteam>
					{this.props.pageGo === undefined ? 'PAGE NOT FOUND!' : this.props.pageGo.object }
				</MainSteam>
					<Output toRender={this.props.pageGo? this.props.pageGo.path : 'mo'}/>
			</CurPage>
		)
	}
}


const PageChange = withRouter(PageInfo);


export default () => {
	return (
		<Switch>
		<Route exact path="/" render={ ({ match }) => (
			<PageChange pageGo={ Info.find( x => x.path === 'home' ) } />
		)}/>
			<Route exact path="/portfolio" render={ ({ match }) => (
				<PageChange pageGo={ Info.find( x => x.path === 'home' ) } />
			)}/>
			<Route path="/portfolio/:id" render={ ({ match }) => (
				<PageChange pageGo={ Info.find( x => x.path === match.params.id.trim().toLowerCase() ) } />
			)
			}/>
		</Switch>
	)
}



