import React, { Component } from 'react';
import Headbar from './Components/headbar';
import Content from './Components/content';
import logo from './logo.svg';
import styled from 'styled-components';
import { connect } from 'react-redux';
import Footer from './Components/footer';

const AppOuter = styled.div`
`

const AppContainer = styled.div`
  position: absolute;
  width: ${ window.innerWidth>960 ? 960 : window.innerWidth }px;
  height: ${ window.innerHeight>650 && window.innerWidth>960 ? 650 : window.innerHeight }px;
  top: ${ window.innerHeight>650 && window.innerWidth>960 ? (window.innerHeight-650)/2 : 0 }px;
  left: ${ window.innerWidth>960 ? (window.innerWidth-960)/2 : 0 }px;
  border: ${ window.innerWidth>980 ? 'solid 10px #222' : 'none' };
  border-radius: 10px;
`


const responsiveData = () => {
  return {
    width: window.innerWidth*0.6,
    start: window.innerWidth*0.15
  }
}

export default () => {
  return (
    <AppOuter>
      <AppContainer>
        <Headbar />
        <Content />
        <Footer />
      </AppContainer>
    </AppOuter>
  )
}



