import React, { Component } from 'react';
import styled from 'styled-components';
import CopyIconBtn from 'react-icons/lib/md/content-copy';
import copy from 'copy-to-clipboard';

const ContentContainer = styled.div`
	display: grid;
	grid-template-columns: ${ window.innerWidth > 960 ? '140px 450px 20px' : '320px' };
	>svg {
		display: ${ window.innerWidth > 960 ? 'inline' : 'none' };
		color: #666;
		font-size: 14px;
		margin: 20px 20px 15px 20px;
		&:hover {
			color: #ccc;
		}
	}
`
const DataType = styled.div`
	font-size: 14px;
	font-weight: bolder;
	margin: 10px 20px 15px 40px;
	margin-left: ${window.innerWidth<=320? '20px' : '40px'} !important;
`
const Output = styled.div`
	font-size: 14px;
	margin: 10px 0px 15px 40px;
	margin-left: ${window.innerWidth<=320? '20px' : '40px'} !important;
	text-align: left;
`

export default () => (
	<ContentContainer>
	<DataType>
	2011 - 2014
	</DataType>
	<Output>
	The Community College at Lingnan University<br/>
	Higher-Diploma in Computer Studies
	</Output>
	<CopyIconBtn onClick={ () => {
		copy('The Community College at Lingnan University - Higher-Diploma in Computer Studies');
	}} />
	<DataType>
	2005 - 2010
	</DataType>
	<Output>
	Yan Oi Tong Chan Wong Suk Fong Memorial Secondary School<br/>
	Completed Form Five
	</Output>
	<CopyIconBtn onClick={ () => {
		copy('Yan Oi Tong Chan Wong Suk Fong Memorial Secondary School - Form Five');
	}} />
	</ContentContainer>
)



